<template>
  <div>
    <!-- table -->
    <vue-good-table
      :columns="columns"
      :rows="sales"
      :search-options="{
        enabled: true,
        externalQuery: searchTerm,
      }"
      :pagination-options="{
        enabled: false,
      }"
    >
      <template slot="table-row" slot-scope="props">
        <div v-if="props.column.field === 'items'" class="text-nowrap">
          <b-list-group>
            <b-list-group-item
              class="d-flex justify-content-between align-items-center itemlist"
              v-for="(item, index) in props.row.items"
              :key="index"
            >
              <span>{{ elementAt("goods", item.id, "plural") }}</span>
              <div class="info">
                <b-badge
                  v-if="elementAt('countries', item.origin, 'name') !== ''"
                  v-b-tooltip
                  class="infobadge infoflag"
                  :title="elementAt('countries', item.origin, 'name')"
                  variant="light-secondary"
                >
                  <!--<feather-icon icon="GlobeIcon" />-->
                  <country-flag
                    :country="elementAt('countries', item.origin, 'code')"
                    size="small"
                    rounded
                  />
                  <small> {{ elementAt("countries", item.origin, "code") }}</small>
                </b-badge>

                <b-badge
                  v-if="elementAt('goodcategories', item.category, 'description') !== ''"
                  v-b-tooltip
                  class="infobadge"
                  :title="elementAt('goodcategories', item.category, 'description')"
                  variant="light-secondary"
                  ><feather-icon icon="AwardIcon" /> <small>{{ item.category }}</small
                  ><!--<span >{{props.row.good_category}}</span>-->
                </b-badge>

                <b-badge
                  v-if="item.cost !== null"
                  v-b-tooltip
                  class="infobadge"
                  variant="light-secondary"
                  ><!--<feather-icon icon="DollarSignIcon" />-->
                  <span
                    >{{
                      item.weight +
                      " Kg. = " +
                      Math.round(item.cost * item.weight * 100) / 100
                    }}<small> €</small></span
                  >
                </b-badge>
              </div>
            </b-list-group-item>
          </b-list-group>
        </div>

        <!--<div v-else-if="props.column.field === 'timestamp'">
          <span class="text-nowrap">{{
            new Date(props.row.timestamp).toLocaleString("it-IT", {
              weekday: "long",
              year: "numeric",
              month: "long",
              day: "numeric",
              timeZone: "Europe/Rome",
              hour12: false,
              hour: "numeric",
              minute: "numeric",
            })
          }}</span>
        </div>-->
      </template>
    </vue-good-table>
  </div>
</template>

<script>
import {
  BAvatar,
  BBadge,
  BPagination,
  BFormGroup,
  BFormInput,
  BFormSelect,
  BDropdown,
  BDropdownItem,
  BButton,
  VBTooltip,
  BListGroup,
  BListGroupItem,
} from "bootstrap-vue";
import { VueGoodTable } from "vue-good-table";
import { elementAt, boxLocationToReadable } from "../utils";

export default {
  components: {
    VueGoodTable,
    BAvatar,
    BBadge,
    BPagination,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BDropdown,
    BDropdownItem,
    BButton,
    BListGroup,
    BListGroupItem,
  },
  directives: {
    "b-tooltip": VBTooltip,
  },
  data() {
    return {
      sales: [],
      pageLength: 3,
      dir: false,
      columns: [
        {
          label: "#",
          field: "number",
          tdClass: "text-center",
          filterOptions: {
            enabled: true,
            placeholder: "Cerca #",
          },
          width: "50px",
        },
        {
          label: "Totale",
          field: "total",
          filterOptions: {
            enabled: true,
            placeholder: "Cerca totale",
          },
          width: "100px",
          formatFn: function (value) {
            var num = Math.round(value * 100) / 100;
            return Number(num).toFixed(2)+" €";
          },
        },
        {
          label: "Data",
          field: "timestamp",
          filterOptions: {
            enabled: false,
            placeholder: "Cerca data",
          },
          formatFn: function (value) {
            return new Date(value).toLocaleString("it-IT", {
              weekday: "long",
              year: "numeric",
              month: "long",
              day: "numeric",
              timeZone: "Europe/Rome",
              hour12: false,
              hour: "numeric",
              minute: "numeric",
            });
          },
        },
        {
          label: "Prodotti",
          field: "items",
          filterOptions: {
            enabled: false,
            placeholder: "Cerca prodotti",
          },
        },
        /* {
          label: "Azioni",
          field: "action",
        }, */
      ],
      searchTerm: "",
    };
  },
  computed: {},
  beforeCreate() {},
  beforeDestroy() {
    const self = this;
    if (self.intervalTimer !== undefined) clearInterval(self.intervalTimer);
  },
  setup() {
    const self = this;
    // console.log(this.$store);
  },
  created() {
    const self = this;
    self.retrieveData();
    self.intervalTimer = window.setInterval(() => {
      self.retrieveData();
    }, 1000);
  },
  methods: {
    retrieveData() {
      const self = this;
      self.$store.dispatch("sales/get").then((result) => {
        console.log(result);
        self.sales = result.data.data;
      });
    },
    boxLocationToReadable,
    elementAt,
    capitalize(text) {
      if (text === undefined) return "";
      return text
        .toLowerCase()
        .split(" ")
        .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
        .join(" ");
    },
  },
};
</script>

<style lang="scss">
@import "@core/scss/vue/libs/vue-good-table.scss";

.info {
  position: relative;
  right: 0px;
}

.itemlist {
  padding: 0.2rem 0.5rem;
}

.infobadge {
  margin-left: 3px;
}

.infobadge.infoflag .small-flag {
  margin: -1.2em -1.5em -1.2em;
}
</style>
